const AuthStep = ({ next }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  // Add these variables to your component to track the state
  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword(!showPassword);
  const handleMouseDownPassword = () => setShowPassword(!showPassword);

  return (
    <Box>
    
      <Typography variant="h3" sx={{ m: 1, }}>Please Log in</Typography>

      <form>
        <TextField 
          id="username" 
          label="Username" 
          variant="outlined"
          sx={{
            m: 1,
            width: '240px',
          }}
          onChange={(event) => setUsername(event.target.value) } 
        />
        <br />
        <TextField 
          id="password" 
          label="Password" 
          variant="outlined" 
          type={showPassword ? "text" : "password"}
          sx={{
            m: 1,
            width: '240px',
          }}
          onChange={(event) => setPassword(event.target.value) }
          InputProps={{ // <-- This is where the toggle button is added.
            endAdornment: (
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                >
                  {showPassword ? <VisibilityIcon /> : <VisibilityOffIcon />}
                </IconButton>
              </InputAdornment>
            )
          }}
        />
      </form>

      <Button 
        variant="contained" 
        sx={{ m: 1, p: 2 }} 
        onClick={() => signInWithEmailAndPwd(username, password, ()=>{
          next()
        }, () => {
          console.log(`error`)
        })}
      >
        Sign In
      </Button>
    
      <Button 
        variant="contained" 
        sx={{ m: 1, p: 2 }} 
        onClick={() => createUserWithEmailAndPwd(username, password)}>
        Create Account
      </Button>

      <Button 
        variant="contained" 
        sx={{ m: 1, p: 2, display: 'none', }} 
        onClick={() => signOut()}
      >
        Sign Out
      </Button>

      <Box>
        <Button variant="outlined" sx={{ m: 1, p: 2 }} onClick={() => getIdTokenAndCall(({ idToken }) => {
          fetch();
          console.log(`getIdTokenAndCall idToken ${idToken}`);
        })}>Test Id Token Fetch</Button>
      </Box>
      
    </Box>
  )
}

