const TakeImg = ({ onImageCapture }) => (<div>
  <Webcam 
    audio={false}
    screenshotFormat="image/jpeg"
    width={720}
    height={480}
  >
    {
      ({ getScreenshot }) => (
        <div
          sx={{
            width: '100%',
          }}
        >
          <Button variant="contained"
            onClick={(event) => {
              event.preventDefault(); // prevent default page refresh behavior
              const imageSrc = getScreenshot() // getScreenshot - Returns a base64 encoded string of the current webcam image. 
              console.log(`TakeImg imageSrc: ${imageSrc}`)
              onImageCapture(imageSrc)
              return false
            }}
          >
            Take photo
          </Button>
        </div>
      )
    }
  </Webcam>
</div>);

