const ImgTakingStep = ({ stepNumber, handleImageCapture, next, back={}, hasBack=false }) => {
  const [hasImgCaptured, setHasImgCaptured] = useState(false)
  return (
    <form>
      <Box>
        <Typography 
          variant="h3"
          sx={{
            m: 1,
            p: 1,
          }}
        >
          Step {stepNumber}
        </Typography>
      </Box>
      <Box
        sx={{
          m: 1,
          minHeight: '480px',
        }}
      >
        {
          hasImgCaptured ? 
            (<Box>
              <Typography>Image Captured.</Typography>
              <Typography>Click Next.</Typography>
            </Box>) : 
            <TakeImg onImageCapture={(img) => {
              handleImageCapture(img)
              setHasImgCaptured(true)
            }}/>
        }
      </Box>

      {hasBack && (<Button variant="contained" sx={{ m: 1, p: 2 }} onClick={back}>Back</Button>)}
      
      <Button variant="contained" sx={{ m: 1, p: 2 }} disabled={!hasImgCaptured} onClick={next}>Next</Button>
    </form>
  );
};

const Step1 = ({ handleChange, handleImageCapture, next }) => (
  <ImgTakingStep
    stepNumber={1}
    handleImageCapture={handleImageCapture}
    handleChange={handleChange}
    next={next}
  />
);

const Step2 = ({ data, handleChange, handleImageCapture, next, back }) => (
  <ImgTakingStep
    stepNumber={2}
    handleImageCapture={handleImageCapture}
    handleChange={handleChange}
    next={next}
    back={back}
    hasBack={true}
  />
);

const Step3 = ({ data, img1, img2, handleChange, next, back }) => {
  const [response, setResponse] = useState({});

  // console.log('img1')
  // console.log(img1)

  // console.log('img2')
  // console.log(img2)

  useEffect(() => {

    console.log(`Calling ${data.url}`);

    getIdTokenAndCall(({ idToken }) => {
      console.log(`getIdTokenAndCall ${idToken} fetch`);

      // POST request using fetch inside useEffect React hook
      const requestOptions = {
        method: 'POST',
        headers: { 
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': 'http://localhost:3000',
          'Access-Control-Allow-Headers': '*',
          'Access-Control-Allow-Methods': "POST,OPTIONS",
          'Authorization': idToken
        },
        body: JSON.stringify({ img1, img2 })
      };

      console.log(`getIdTokenAndCall > data.url ${data.url}`);

      fetch(data.url, requestOptions)
        .then(response => response.json())
        .then(data => setResponse(data))
        .catch((e) => console.error(e));
    })

  // empty dependency array means this effect will only run once (like componentDidMount in classes)
  }, [img1, img2, data]);

  return (
    <form>
        <Typography 
          variant="h3"
          sx={{
            m: 1,
            p: 1,
          }}
        >
          Step 3 - View result
        </Typography>
      
      <ShowResult result={response}/>
      <Button variant="contained" sx={{ m: 1, p: 2 }} onClick={back}>Back</Button> 
      <Button variant="contained" sx={{ m: 1, p: 2 }} onClick={next}>Next</Button>
    </form>
  );
};

const Submit = ({ data, back, inspectAnotherItem, registerANewOne }) => {
  const listItems = Object.entries(data).map(([key, value]) => (
    <li>
      <strong>{key}:</strong> {value}
    </li>
  ));
  return (
    <div>
      <ul>{listItems}</ul>
      <Button variant="contained" sx={{ m: 1, p: 2 }} type="submit">Submit</Button>
      <Button variant="contained" sx={{ m: 1, p: 2 }} onClick={inspectAnotherItem}>Inspect another item</Button>
      <Button variant="contained" sx={{ m: 1, p: 2 }} onClick={registerANewOne}>Register a new one</Button>
      <Button variant="contained" sx={{ m: 1, p: 2 }} onClick={back}>Back</Button> 
    </div>
  );
};

const MultiStepForm = () => {
  const [currentStep, setCurrentStep] = useState(-1); // non-zero indexing
  const [formData, setFormData] = useState({ name: '', url: '' }); // form data stored here
  const [img1, setImg1] = useState('');
  const [img2, setImg2] = useState('');

  const handleChange = (event) => {
    console.log(`handleChange name: ${event.target.name} value: ${event.target.value}`)
    setFormData({
      ...formData,
      [event.target.name]: event.target.value
    })
  };

  const next = () => setCurrentStep(currentStep + 1)
  const back = () => setCurrentStep(currentStep - 1)
  const inspectAnotherItem = () => setCurrentStep(2)
  const registerANewOne = () => setCurrentStep(1)
  
  switch (currentStep) {
    case -1:
      return (<AuthStep next={next}/>)
    
    case 0:
      return (
        <Step0
          handleChange={handleChange}
          next={next}
        />
      )
    case 1:
      return (
        <Step1 
          data={formData}
          handleChange={handleChange}
          handleImageCapture={(imgData) => { setImg1(imgData); console.log(`case 1: handleImageCapture imgData: ${imgData}`)}}
          next={next}
        />
      )
    case 2:
      return (
        <Step2
          data={formData}
          handleChange={handleChange}
          handleImageCapture={(imgData) => { setImg2(imgData); console.log(`case 2: handleImageCapture imgData: ${imgData}`)}}
          next={next}
          back={back}
        />
      )
    case 3:
      return (
        <Step3
          data={formData}
          img1={img1}
          img2={img2}
          handleChange={handleChange}
          next={next}
          back={back}
        />
      );
    default:
      return (<Submit 
        data={formData} 
        back={back}
        inspectAnotherItem={inspectAnotherItem}
        registerANewOne={registerANewOne}
      />);
  }
}

