import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword } from "firebase/auth";

export const signOut = async () => {
    const auth = getAuth();
    auth.signOut(auth).then(() => {
        console.log(`Sign-out successful.`);
        return true; // TODO verify
    }).catch((error) => {
        // An error happened.
        throw error; // TODO verify
    });
};

export const signInWithEmailAndPwd = async (email, password, onSuccess, onError) => {
    const auth = getAuth();
    signInWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        // Signed in 
        const user = userCredential.user;
        if (onSuccess) {
            onSuccess(user);
        }

        // TODO: Add redux to save user state
    })
    .catch((error) => {
        const errorCode = error.code;
        console.log(`signInWithEmailAndPwd: errorCode ${errorCode}`);

        const errorMessage = error.message;
        console.log(`signInWithEmailAndPwd: errorMessage ${errorMessage}`);

        alert(`SignInWithEmailAndPassword: \nerrorCode: ${errorCode}\n errorMessage: ${errorMessage}`);
        
        if (onError) {
            onError(error);
        }

        throw error;
    });
}

export const createUserWithEmailAndPwd = async (email, password, onSuccess, onError) => {
    const auth = getAuth();
    createUserWithEmailAndPassword(auth, email, password)
    .then((userCredential) => {
        // Signed in 
        const user = userCredential.user;
        
        // TODO: Add redux to save user state
        onSuccess(userCredential);
    })
    .catch((error) => {
        const errorCode = error.code;
        console.log(`createUserWithEmailAndPwd: errorCode ${errorCode}`);

        const errorMessage = error.message;
        console.log(`createUserWithEmailAndPwd: errorMessage ${errorMessage}`);

        alert(`CreateUserWithEmailAndPwd: \nerrorCode: ${errorCode} \nerrorMessage: ${errorMessage} `);

        throw error;
    });
}
